    display: flex;
  }

  .lg\:hidden {
    display: none;
  }

  .lg\:h-\[32rem\] {
    height: 32rem;
  }

  .lg\:h-\[44rem\] {
    height: 44rem;
  }

  .lg\:w-1\/2 {
